.mainbox {
    background-color: #FFFCFC;
    height : inherit;
	margin:10%;
	border:solid;
    border-width : 5px;
    border-color: darkslategrey; 
    border-radius: 28px;
    padding-bottom: 5%;

}
.inputDate {
    margin-left:30%;
    width : 200px;
    padding-right:5%;
    height:50px;
    color:#127681 ;
    border-radius:25px;
    padding-left:4%;
    border-width:3px;
}

.myButton {
	
	background-color:#163948;
	border-radius:5px;
	border:1px solid #d6bcd6;
	display:inline-block;
	cursor:pointer;
	color:white;
	font-family:Arial;
	font-size:20px;
	padding:12px 29px;
	text-decoration:none;
	text-shadow:0px 1px 0px #e1e2ed;
}
.myButton:hover {

	background-color:#DDADA6;
	color: white;
	text-decoration: none;
}
.myButton:active {
	position:relative;
	top:1px;
}





